// ===== contact helpers =====
const WHATSAPP_NUM = "917219243544"; // +91 7219243544
const WA_TEXT = "Hello IT Professional Lab! Im interested in your courses.";

// build WA link safely
function useWhatsAppHref() {
  return useMemo(() => {
    const u = new URL(`https://wa.me/${WHATSAPP_NUM}`);
    u.searchParams.set("text", WA_TEXT);
    return u.toString();
  }, []);
}

